import { Box, Typography } from "@mui/material"
import { useContext } from "react"
import { useTranslation } from "@fanfanlo"
import { ActionListOrTriggerListPageParamsContext, ActionOrTriggerGroupListContext } from "./context"
import { IActionOrTriggerItem, IActionOrTriggerGroup, IActionOrTriggerGroupList } from "./ITriggerList-IActionList-common.intf"
import { IActionOrTriggerListPageParams } from "@app/static"
import { AppbarContainer, ITitleI18nConf } from "@app/ui"



function Item({ item }: { item: IActionOrTriggerItem }) {
    const { t } = useTranslation(item.i18nNs as any)
    const params = useContext(ActionListOrTriggerListPageParamsContext)
    return (
        <Box sx={{ width: "100%", textIndent: "1rem", padding: "1px", backgroundColor: "#f6f2e9" }}>
            <Typography>
                <item.LinkRender query={params} id={item.id} item={item}>
                    {t(item.nameKey)}
                </item.LinkRender>
                {/* <span onClick={() => item.onSelect(router)}>{t(item.nameKey)}</span> */}
            </Typography>

        </Box>
    )
}

function Group({ group }: { group: IActionOrTriggerGroup }) {
    const { t } = useTranslation(group.i18nNs as any)
    return (
        <Box sx={{ width: "100%" }}>
            <Box sx={{ width: "100%", backgroundColor: "#f6f9f6", padding: "2px" }}>
                <span>{t(group.nameKey)}</span>
            </Box>
            {group.items.map(action => {
                return (
                    <Item key={action.i18nNs} item={action} />
                )
            })}
        </Box>
    )
}
function GroupList() {
    const groups = useContext(ActionOrTriggerGroupListContext)
    return (
        <Box sx={{ width: "100%" }}>
            {groups.map((group) => (
                <Group key={group.i18nNs} group={group} />
            ))}
        </Box>
    )
}

function ActionListContent() {
    return (
        <Box>
            <GroupList />
        </Box>
    )
}

export function TriggerListOrActionListUI({ groups, title, actionListParams, titleConf }: { groups: IActionOrTriggerGroupList, title:React.ReactNode, actionListParams: IActionOrTriggerListPageParams, titleConf?:ITitleI18nConf }) {
    return (
        <ActionListOrTriggerListPageParamsContext value={actionListParams}>
            <ActionOrTriggerGroupListContext value={groups}>
            <AppbarContainer appbarProps={{
                title: <Typography>
                    {title}
                </Typography>,
                back: true
            }}
            titleConf={titleConf}>
            <ActionListContent />
            </AppbarContainer>
            </ActionOrTriggerGroupListContext>
        </ActionListOrTriggerListPageParamsContext>
    )
}