import { IActionOrTriggerListPageParams } from '@app/static';
import { useTranslation } from '@fanfanlo';
import { actionListGroups } from '@src/components/page/action-list/action.data';
import { TriggerListOrActionListUI } from '@src/components/trigger-list-action-list-common/TriggerListOrActionListUI';
import { useRouter } from 'next/router';
import { AppShell } from '@app/ui';

export default function index() {
  const router = useRouter()
  const { t } = useTranslation("homepage/pages/action-list/content")
  return (
    <AppShell>
      <TriggerListOrActionListUI groups={actionListGroups} title={t("content.title")} actionListParams={router.query as IActionOrTriggerListPageParams} titleConf={{title:t("content.title")}} />
    </AppShell>
  )   
}

